import React, { useState } from 'react'
import { Dialog, DialogTitle, Button, Box, Stack, Switch, TextField, FormControlLabel, FormControl, Select, MenuItem, InputLabel } from '@mui/material'
import { LocalizationProvider, TimePicker, MobileDatePicker } from '@mui/x-date-pickers';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import CheckWeeks from './FreqCompo/CheckWeeks';
import dayjs from 'dayjs';
import axios from 'axios';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import Progress from './FreqCompo/Progress';
import Snackbar from './FreqCompo/Snackbar';
import MapAPI from './MapAPI';
import LabelBottomNavigation from './LabelBottomNavigation';

const AddEvent = () => {
  const [fontColor,] = useState("white"); //    
  const [startTagColor,] = useState("#2bcdb2"); //     
  const [openTag, setOpenTag] = useState(false);
  const [openTag2, setOpenTag2] = useState(false);
  const [openCategory, setOpenCategory] = useState(false);
  const [tag, setTag] = useState({ tagName: "", tagColor: startTagColor });
  const [tag2, setTag2] = useState({ tagName: "", tagColor: startTagColor });
  const nav = useNavigate();
  const dispatch = useDispatch();
  const email = useSelector(state => (state.session.email)); //     email  
  const [checkWeeks, setCheckWeeks] = useState({ mon: false, tue: false, wed: false, thu: false, fri: false, sat: false, sun: false });
  const [eventInfo, setEventInfo] = useState({ //title, 
  });
  const [checkSpecial, setCheckSpecial] = useState(false);
  const [preAlarm, setPreAlarm] = useState(0);
  const [checkRecommend, setCheckRecommend] = useState(false);
  const [sLocation, setSLocation] = useState('');
  const [eLocation, setELocation] = useState('');

  const handleOpenTag = () => {
    setOpenTag(true);
  }
  const handleOpenTag2 = () => {
    setOpenTag2(true);
  }
  const [cateList, setCateList] = useState([]);
  const [start, setStart] = useState(dayjs(new Date()));
  const [sTime, setSTime] = useState('');
  const [end, setEnd] = useState(dayjs(new Date()));
  const [eTime, setETime] = useState('');

  const handleStartDate = (n) => {
    console.log(n)
    setStart(n);
  }
  const handleEndDate = (n) => {
    setEnd(n);
  }
  const handleStartTime = (n) => {
    let hour = n.$H;
    let minute = n.$m;
    hour = hour < 10 ? '0' + hour : hour;
    minute = minute < 10 ? '0' + minute : minute;
    
    setStart(n);
    setSTime(hour + ':' + minute);
  };
  const handleEndTime = (n) => {
    let hour = n.$H;
    let minute = n.$m;
    hour = hour < 10 ? '0' + hour : hour;
    minute = minute < 10 ? '0' + minute : minute;
    setEnd(n);
    setETime(hour + ':' + minute);
  };

  const handleCategory = (e) => {
    let count = 0
    if (cateList.length !== 0) {
      for (let i of cateList) {
        if (i === e.target.name) {
          count += 1
        } else {

        }
      }
      if (count == 0) {
        setCateList([...cateList, e.target.name]); setOpenCategory(false)
      }
    } else {
      setCateList([...cateList, e.target.name]); setOpenCategory(false)
    }
  }

  const handleEventInfo = (e) => {
    setEventInfo({ ...eventInfo, [e.target.name]: e.target.value })
  }
  const handleFormSubmit = () => {
    let sDay = start.$y +'-'+ (start.$M + 1) +'-'+ start.$D;
    let eDay = end.$y +'-'+ (end.$M + 1) +'-'+ end.$D;
    const url = "http://localhost:5000/lifeConcierge/api/addEvent";
    console.log(tag.tagName)

    let tempTag;
    let tempColor;
    if (tag.tagName !== '') {
      tempTag = tag.tagName;
      tempColor = tag.tagColor;
    } else {
      tempTag = tag2.tagName;
      tempColor = tag2.tagColor;
    }
    console.log(tempTag, tempColor)
    if (email) {
      dispatch({ type: "PROGRESS", progress: { progressToggle: true } });
      axios.post(url, { ...eventInfo, checkWeeks, email, checkSpecial, preAlarm, tag : tempTag, color: tempColor, cateList, start: sDay, end: eDay, sTime, eTime, sLocation, eLocation })
        .then((res) => {
          if (res.data.affectedRows) {
            dispatch({ type: "ISEVENTADDED", isEventAdded: true });
            dispatch({ type: "PROGRESS", progress: { progressToggle: false } });
            nav('/calendar');
            
          } else {
            dispatch({ type: "SNACKBAR/ON", snackbar: { snackbarToggle: true, explain: " ", severity: "error" } });
            dispatch({ type: "PROGRESS", progress: { progressToggle: false } });
          }
        })
        .catch((err) => { console.log(" ") })
    } else {
      dispatch({ type: "SNACKBAR/ON", snackbar: { snackbarToggle: true, explain: "  .   .", severity: "error" } });
    }

  }

  return (
    <Box className='test'>
      <Button onClick={handleOpenTag} style={{ background: tag.tagColor, color: fontColor }}>{tag.tagName ? tag.tagName : ""}</Button>
      {tag.tagName == "" ? <Button onClick={handleOpenTag2} style={{ background: tag2.tagColor, color: fontColor }}>{tag2.tagName ? tag2.tagName : ""}</Button> : ""}
      <Stack spacing={1}>
        <TextField size="small" label="" name="title" variant="standard" sx={{ mb: "20px" }} onChange={handleEventInfo} />
        {tag.tagName == "" ? "" : <FormControlLabel control={<Switch name="checkSpecial" onChange={() => { setCheckSpecial(!checkSpecial) }} />} label="" />}<br />
        <LocalizationProvider dateAdapter={AdapterDayjs}>
          <MobileDatePicker
            label=" "
            inputFormat="MM/DD/YYYY"
            onChange={handleStartDate}
            value={start}
            renderInput={(label) => <TextField size="small" {...label} />}
          />
          <TimePicker
            label=" "
            value={start}
            onChange={handleStartTime}
            renderInput={(label) => <TextField size="small" {...label} />} />
        </LocalizationProvider>
        <br />
        <LocalizationProvider dateAdapter={AdapterDayjs}>
          <MobileDatePicker
            label=" "
            inputFormat="MM/DD/YYYY"
            onChange={handleEndDate}
            value={end}
            renderInput={(label) => <TextField size="small" {...label} />}
          />
          <TimePicker
            label=" "
            value={end}
            onChange={handleEndTime}
            renderInput={(label) => <TextField size="small" {...label} />} />
        </LocalizationProvider>
        <br />
        <FormControl variant='filled' size='small' sx={{ width: "200px", mt: "0" }}>
          <InputLabel id="demo-simple-select-label"> </InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={preAlarm}
            onChange={(e) => { setPreAlarm(e.target.value) }}
          >
            <MenuItem value={0}></MenuItem>
            <MenuItem value={10}>10</MenuItem>
            <MenuItem value={20}>20</MenuItem>
            <MenuItem value={30}>30</MenuItem>
            <MenuItem value={60}>60</MenuItem>
          </Select>
        </FormControl>
        <MapAPI sLocation={sLocation} setSLocation={setSLocation}
          eLocation={eLocation} setELocation={setELocation}>
        </MapAPI>
        {tag.tagName == "" ? <CheckWeeks checkWeeks={checkWeeks} setCheckWeeks={setCheckWeeks} /> : ""}

        {/* <TextField size="small" label="" name="content" multiline rows={5} variant="outlined" style={{marginBottom:"20px"}} onChange={handleEventInfo}/> */}
        <TextField size="small" label="" name="content" multiline rows={3} variant="outlined" sx={{ mb: "20px" }} onChange={handleEventInfo} />
        {/* <TextField label="" name="location" variant="outlined" sx={{mb:"20px"}} onChange={handleEventInfo}/> */}
      </Stack>
      <FormControlLabel style={{ float: "right" }} control={<Switch name="checkRecommend" onChange={() => { setCheckRecommend(!checkRecommend) }} />} label="" /><br />
      {checkRecommend ?
        <div>
          {cateList.map((data, index) => (
            <Button name={data} key={index} onClick={(e) => { setCateList(cateList.filter((data) => (data !== e.target.name))) }}>{data}</Button>
          ))}
          <Button style={{ border: "solid black" }} onClick={() => { setOpenCategory(true) }}>+</Button>
        </div>
        : ""}<br />
      <Button sx={{ mt: "10px", float: "right" }} variant="contained" onClick={handleFormSubmit}></Button>

      <Snackbar />
      <Progress />
      {/*  1    */}
      <Dialog open={openTag} onClose={() => { setOpenTag(false) }}>
        <DialogTitle>
           
        </DialogTitle>
        <DialogTitle>
          <Button name="" style={{ background: "#94ab50", color: fontColor }} onClick={(e) => { setTag({ ...tag, tagName: e.target.name, tagColor: "#94ab50" }); setOpenTag(false) }}></Button><br />
          <Button name="/" style={{ background: "#ffc847", color: fontColor }} onClick={(e) => { setTag({ ...tag, tagName: e.target.name, tagColor: "#ffc847" }); setTag2({ ...tag2, tagName: "", tagColor: startTagColor }); setOpenTag(false); setCheckWeeks({ mon: false, tue: false, wed: false, thu: false, fri: false, sat: false, sun: false }) }}>/</Button><br />
          <Button name="//" style={{ background: "#8ed4c9", color: fontColor }} onClick={(e) => { setTag({ ...tag, tagName: e.target.name, tagColor: "#8ed4c9" }); setTag2({ ...tag2, tagName: "", tagColor: startTagColor }); setOpenTag(false); setCheckWeeks({ mon: false, tue: false, wed: false, thu: false, fri: false, sat: false, sun: false }) }}>//</Button><br />
          <Button name="/" style={{ background: "#855e95", color: fontColor }} onClick={(e) => { setTag({ ...tag, tagName: e.target.name, tagColor: "#855e95" }); setTag2({ ...tag2, tagName: "", tagColor: startTagColor }); setOpenTag(false); setCheckWeeks({ mon: false, tue: false, wed: false, thu: false, fri: false, sat: false, sun: false }) }}>/</Button><br />
          <Button name="//" style={{ background: "#e6c2ce", color: fontColor }} onClick={(e) => { setTag({ ...tag, tagName: e.target.name, tagColor: "#e6c2ce" }); setTag2({ ...tag2, tagName: "", tagColor: startTagColor }); setOpenTag(false); setCheckWeeks({ mon: false, tue: false, wed: false, thu: false, fri: false, sat: false, sun: false }) }}>//</Button><br />
          <Button name="/" style={{ background: "#c7d3a5", color: fontColor }} onClick={(e) => { setTag({ ...tag, tagName: e.target.name, tagColor: "#c7d3a5" }); setTag2({ ...tag2, tagName: "", tagColor: startTagColor }); setOpenTag(false); setCheckWeeks({ mon: false, tue: false, wed: false, thu: false, fri: false, sat: false, sun: false }) }}>/</Button><br />
          <Button name="/" style={{ background: "#477e85", color: fontColor }} onClick={(e) => { setTag({ ...tag, tagName: e.target.name, tagColor: "#477e85" }); setTag2({ ...tag2, tagName: "", tagColor: startTagColor }); setOpenTag(false); setCheckWeeks({ mon: false, tue: false, wed: false, thu: false, fri: false, sat: false, sun: false }) }}>/</Button><br />
        </DialogTitle>
      </Dialog>
      {/*  2    */}
      <Dialog open={openTag2} onClose={() => { setOpenTag2(false) }}>
        <DialogTitle>
           
        </DialogTitle>
        <DialogTitle>
          <Button name="/" style={{ background: "#ffc847", color: fontColor }} onClick={(e) => { setTag2({ ...tag2, tagName: e.target.name, tagColor: "#ffc847" }); setOpenTag2(false) }}>/</Button><br />
          <Button name="//" style={{ background: "#8ed4c9", color: fontColor }} onClick={(e) => { setTag2({ ...tag2, tagName: e.target.name, tagColor: "#8ed4c9" }); setOpenTag2(false) }}>//</Button><br />
          <Button name="/" style={{ background: "#855e95", color: fontColor }} onClick={(e) => { setTag2({ ...tag2, tagName: e.target.name, tagColor: "#855e95" }); setOpenTag2(false) }}>/</Button><br />
          <Button name="//" style={{ background: "#e6c2ce", color: fontColor }} onClick={(e) => { setTag2({ ...tag2, tagName: e.target.name, tagColor: "#e6c2ce" }); setOpenTag2(false) }}>//</Button><br />
          <Button name="/" style={{ background: "#c7d3a5", color: fontColor }} onClick={(e) => { setTag2({ ...tag2, tagName: e.target.name, tagColor: "#c7d3a5" }); setOpenTag2(false) }}>/</Button><br />
          <Button name="/" style={{ background: "#477e85", color: fontColor }} onClick={(e) => { setTag2({ ...tag2, tagName: e.target.name, tagColor: "#477e85" }); setOpenTag2(false) }}>/</Button><br />
        </DialogTitle>
      </Dialog>

      <Dialog open={openCategory} onClose={() => { setOpenCategory(false) }}>
        <DialogTitle>
           
        </DialogTitle>
        <DialogTitle>
          <div>
            <Button name="//" style={{ background: "#ffc847", color: fontColor }} onClick={handleCategory}>//</Button><br />
            <Button name="//" style={{ background: "#22c847", color: fontColor }} onClick={handleCategory}>//</Button>
            <Button name="" style={{ background: "#2293c8", color: fontColor }} onClick={handleCategory}></Button>
            <Button name="//" style={{ background: "#96c822", color: fontColor }} onClick={handleCategory}>//</Button>
            <Button name="/" style={{ background: "#c822af", color: fontColor }} onClick={handleCategory}>/</Button>
            <Button name="/" style={{ background: "#c82257", color: fontColor }} onClick={handleCategory}>/</Button>
            <Button name="/" style={{ background: "#c8a722", color: fontColor }} onClick={handleCategory}>/</Button>
            <Button name="//" style={{ background: "#1d0303", color: fontColor }} onClick={handleCategory}>//</Button>
            <Button name="/" style={{ background: "#2d4e68", color: fontColor }} onClick={handleCategory}>/</Button>
            <Button name="/" style={{ background: "#7e54c6", color: fontColor }} onClick={handleCategory}>/</Button>
            <Button name="/" style={{ background: "#e38e27", color: fontColor }} onClick={handleCategory}>/</Button>
            <Button name=" " style={{ background: "#8d2d19", color: fontColor }} onClick={handleCategory}> </Button>
          </div>
        </DialogTitle>
      </Dialog>
      <LabelBottomNavigation></LabelBottomNavigation>
    </Box>
  )
}
export default AddEvent;