import React, { useRef, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { FormControl, TextField, Button } from '@mui/material';
import SnackBar from './FreqCompo/Snackbar';
import Progress from './FreqCompo/Progress'
/* import MessageIcon from '@mui/icons-material/Message';
import { Link } from 'react-router-dom'; */
import greenee from '../img/greenee.png';
import '../css/Login.css';
import axios from 'axios';

const Login = () => {
  const nav = useNavigate();
  const emailRef = useRef(); //  email input   Ref  
  const pwRef = useRef(); //  pw input   Ref
  const dispatch = useDispatch();

  useEffect(() => {
    emailRef.current.focus();    //  email input    
  }, []);

  const checkLogin = () => {  //   email, password DB  
    const url = "/lifeConcierge/api/login";
    dispatch({ type: "PROGRESS", progress: { progressToggle: true } });
    axios.post(url, { email: emailRef.current.value, pw: pwRef.current.value })
      .then((res) => {
        if (res.data != "NoneId") {
          dispatch({ type: "ISLOGGEDIN", isLoggedin: true });
          // Login        MyProfile  Login  MyProfile    Login      .
          //  cofirm     MyProfile
          dispatch({ type: "PROGRESS", progress: { progressToggle: false } });
          dispatch({ type: "SESSION", session: res.data.rows[0] });
        } else {
          dispatch({ type: "SNACKBAR/ON", snackbar: { snackbarToggle: true, explain: " .    .", severity: "error" } });
          dispatch({ type: "PROGRESS", progress: { progressToggle: false } });
        }
      })
      .catch(() => {
        dispatch({ type: "SNACKBAR/ON", snackbar: { snackbarToggle: true, explain: "  .", severity: "error" } });
        dispatch({ type: "PROGRESS", progress: { progressToggle: false } });
      });
    emailRef.current.value = "";
    pwRef.current.value = "";
  }

  return (
    <>
      
      {/* <div style={{ backgrounImage : "url(" + greenihead + ")" }}> 
      <img src='../img/greenihead.png' className='greenihead'></img>   
      <div style={{backgrounImage : "url(" + greenihead + ")"  }}>*/}
      <div style={{ padding: "20px", backgroundColor: '#D7EDBC' }}>
        {/* <div className='divLogin' style={{    width:"80%", border:"1px solid black", padding:"20px"}}>  */}
        <div className='divLogin' style={{marginTop:'40px'}}>
        <img src={greenee} className={'greenee'} style={{ width: "60%", padding: "20px" }} />
        <h60 className='greeniWelcome'> Hello, My name is Greenee.</h60>
        <FormControl fullWidth>
          <TextField variant="standard" inputRef={emailRef} label='' name='email' onChange={(e) => { emailRef.current.value = e.target.value }} /><br></br>
          <TextField variant="standard" inputRef={pwRef} label='' name='pw' type="password" onChange={(e) => { pwRef.current.value = e.target.value; }} /><br></br>
          <Button variant="contained" onClick={checkLogin} className='login_btn'></Button>
          <Button variant="contained" onClick={() => { nav("/signup") }} className='signup_btn' ></Button>
        </FormControl>
        <SnackBar />
        <Progress />
        </div>
      </div>
    </>
  )
}

export default Login;
