// npm install react-kakao-maps-sdk !
//   yarn add react-kakao-maps-sdk
import React, { useEffect, useState, useRef } from "react";
import { TextField } from '@mui/material'
import { Dialog } from '@mui/material';
import Slide from '@mui/material/Slide';
import "../css/map.css"


const { kakao } = window;

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const MapAPI = ({sLocation, eLocation, setSLocation, setELocation}) => {
  const [sLabel, setSLabel] = useState(' ');
  const [eLabel, setELabel] = useState(' ');
  
  const inputLabel = () => {
    if (flag === '') {
      setSLabel('');
    } else {
      setELabel('');
    }
    setSLocation(startAddr);
    setELocation(endAddr);
    
    handleCheckClose();
    handleClose();
  }

  //     
  const [openMap, setOpenMap] = useState(false);

  //     
  const [openCheck, setOpenCheck] = useState(false);

  // ,  (   )
  const [startAddr, setStartAddr] = useState('');
  const [endAddr, setEndAddr] = useState('');

  // ,  (  )
  const [startUserAddr, setStartUserAddr] = useState(sLocation);
  const [endUserAddr, setEndUserAddr] = useState(eLocation);

  //    
  const searchButton = useRef();

  //     
  const [flag, setFlag] = useState('');

  // map  
  const handleOpenStart = () => {
    setFlag('');
    setOpenMap(true);
  }
  const handleOpenEnd = () => {
    setFlag('')
    setOpenMap(true);
  }
  const handleClose = () => {
    setOpenMap(false);
  }

  // check  
  const handleCheckOpen = () => {
    setOpenCheck(true);
  }
  const handleCheckClose = () => {
    setOpenCheck(false);
  }

  //    
  var map;

  //   
  var markers = [];

  var ps = new kakao.maps.services.Places();

  //          
  var infowindow = new kakao.maps.InfoWindow({ zIndex: 1 });

  //       useEffect .
  useEffect(() => {
    //         
    //  setTimeout 
    const st = setTimeout(() => {
      //           
      if (openMap) {
        var mapContainer = document.getElementById('map'), //   div 
          mapOption = {
            center: new kakao.maps.LatLng(37.566826, 126.9786567), //  
            level: 3 //   
          };

        //      
        map = new kakao.maps.Map(mapContainer, mapOption);

        //    
        const searchPlaces = () => {

          var keyword = document.getElementById('keyword').value;

          if (!keyword.replace(/^\s+|\s+$/g, '')) {
            alert(' !');
            return false;
          }

          //      
          ps.keywordSearch(keyword, placesSearchCB);
        }



        //      
        function placesSearchCB(data, status, pagination) {
          if (status === kakao.maps.services.Status.OK) {

            //   
            //    
            displayPlaces(data);

            //   
            displayPagination(pagination);

          } else if (status === kakao.maps.services.Status.ZERO_RESULT) {

            alert('   .');
            return;

          } else if (status === kakao.maps.services.Status.ERROR) {

            alert('    .');
            return;

          }
        }

        //      
        function displayPlaces(places) {

          var listEl = document.getElementById('placesList'),
            menuEl = document.getElementById('menu_wrap'),
            fragment = document.createDocumentFragment(),
            bounds = new kakao.maps.LatLngBounds(),
            listStr = '';

          //      
          removeAllChildNods(listEl);

          //     
          removeMarker();

          for (var i = 0; i < places.length; i++) {

            //    
            var placePosition = new kakao.maps.LatLng(places[i].y, places[i].x),
              marker = addMarker(placePosition, i),
              itemEl = getListItem(i, places[i]); //    Element 

            //       
            // LatLngBounds   
            bounds.extend(placePosition);

            //    mouseover 
            //     
            // mouseout    

            // **    ->   
            (function (marker, title) {
              kakao.maps.event.addListener(marker, 'mouseover', function () {
                displayInfowindow(marker, title);
              });

              kakao.maps.event.addListener(marker, 'click', function () {

                displayInfowindow(marker, title);
              });


              kakao.maps.event.addListener(marker, 'mouseout', function () {
                infowindow.close();
              });

              itemEl.onmouseover = function () {
                displayInfowindow(marker, title);
              };

              itemEl.onclick = function () {
                displayInfowindow(marker, title);
              };

              // itemEl.onmouseout = function () {
              //   infowindow.close();
              // };
            })(marker, places[i].place_name);

            fragment.appendChild(itemEl);
          }

          //     Element 
          listEl.appendChild(fragment);
          menuEl.scrollTop = 0;

          //       
          // console.log(map, bounds)
          map.setBounds(bounds);
        }

        //   Element  
        function getListItem(index, places) {

          var el = document.createElement('li'),
            itemStr = '<span class="markerbg marker_' + (index + 1) + '"></span>' +
              '<div class="info">' +
              '   <h3>' + places.place_name + '</h3>';

          if (places.road_address_name) {
            itemStr += '    <span>' + places.road_address_name + '</span>' +
              '   <span class="jibun gray">' + places.address_name + '</span>';
          } else {
            itemStr += '    <span>' + places.address_name + '</span>';
          }

          itemStr += '  <span class="tel">' + places.phone + '</span>' +
            '</div>';

          el.innerHTML = itemStr;
          el.className = 'item';

          //    ,  
          const button = document.createElement('button');
          button.innerText = '';
          button.addEventListener('click', () => {
            setOpenCheck(true);
          });

          //     li 
          el.appendChild(button);


          //        
          el.addEventListener('click', (e) => {

            //   
            if (flag === '') {
              //    
              setStartUserAddr(places.place_name);

              //    
              if (places.road_address_name) {
                //   
                setStartAddr(places.road_address_name);
              } else {
                //   
                setStartAddr(places.address_name);
              }

            } else {
              setEndUserAddr(places.place_name);
              if (places.road_address_name) {
                setEndAddr(places.road_address_name);
              } else {
                setEndAddr(places.address_name);
              }
            }
          })

          return el;
        }

        //       
        function addMarker(position, idx, title) {
          var imageSrc = 'https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/marker_number_blue.png', //   url,   
            imageSize = new kakao.maps.Size(36, 37),  //   
            imgOptions = {
              spriteSize: new kakao.maps.Size(36, 691), //   
              spriteOrigin: new kakao.maps.Point(0, (idx * 46) + 10), //       
              offset: new kakao.maps.Point(13, 37) //      
            },
            markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imgOptions),
            marker = new kakao.maps.Marker({
              position: position, //  
              image: markerImage
            });

          marker.setMap(map); //    
          markers.push(marker);  //    

          return marker;
        }

        //       
        function removeMarker() {
          for (var i = 0; i < markers.length; i++) {
            markers[i].setMap(null);
          }
          markers = [];
        }

        //      
        function displayPagination(pagination) {
          var paginationEl = document.getElementById('pagination'),
            fragment = document.createDocumentFragment(),
            i;

          //    
          while (paginationEl.hasChildNodes()) {
            paginationEl.removeChild(paginationEl.lastChild);
          }

          for (i = 1; i <= pagination.last; i++) {
            var el = document.createElement('a');
            el.href = "#";
            el.innerHTML = i;

            if (i === pagination.current) {
              el.className = 'on';
            } else {
              el.onclick = (function (i) {
                return function () {
                  pagination.gotoPage(i);
                }
              })(i);
            }

            fragment.appendChild(el);
          }
          paginationEl.appendChild(fragment);
        }

        //        
        //   
        function displayInfowindow(marker, title) {
          var content = '<div style="padding:5px;z-index:1;">' + title + '</div>';

          infowindow.setContent(content);
          infowindow.open(map, marker);
        }

        //    Element  
        function removeAllChildNods(el) {
          while (el.hasChildNodes()) {
            el.removeChild(el.lastChild);
          }
        }
        searchButton.current.onclick = searchPlaces;

      }
    }, 1000)
  }, [openMap])

  return (
    <>
      <TextField 
        size="small" variant="standard" sx={{ mb: "20px" }} 
        value={startUserAddr} label={sLabel} name="sLocation" 
        onClick={handleOpenStart}
      />

      <TextField 
        size="small" variant="standard" sx={{ mb: "20px" }} 
        value={endUserAddr} label={eLabel} name="eLocation" 
        onClick={handleOpenEnd}
      />

      <Dialog open={openMap} onClose={handleClose} fullScreen TransitionComponent={Transition}>
        <div id="menu_wrap" className="bg_white">

          <div id="map" style={{ width: "100%", height: "60%", overflow: "hidden" }}>
          </div>
          <div className="option">
            <div>
              <button onClick={handleClose}>  </button>
              <input type="text" id="keyword" size="15" />
              <button ref={searchButton}></button>
            </div>
          </div>

          <ul id="placesList"></ul>
          <div id="pagination"></div>
        </div>
      </Dialog>

      <Dialog open={openCheck} onClose={handleCheckClose} TransitionComponent={Transition}>
          {flag} ?
        <button onClick={inputLabel}></button>
        <button onClick={handleCheckClose}></button>
      </Dialog>

    </>
  );
};

export default MapAPI;